function handleMap(x, tester, map) {
    if (!x)
        return undefined;
    if (tester(x))
        return x;
    if (map) {
        let mapped = map(x);
        if (mapped && tester(mapped))
            return mapped;
    }
}
export async function loadUUID(uuid, tester, map) {
    if (tester(uuid))
        return uuid;
    let object = await fromUuid(uuid);
    return handleMap(object, tester, map);
}
export function loadUUIDSync(uuid, tester, map) {
    if (tester(uuid))
        return uuid;
    let object = fromUuidSync(uuid);
    return handleMap(object, tester, map);
}
function typedLoad(tester, map) {
    let value = ((uuid) => loadUUID(uuid, tester, map));
    value.sync = (uuid) => loadUUIDSync(uuid, tester, map);
    return value;
}
export function isActor(value) { return value instanceof Actor; }
export const loadActor = typedLoad(isActor, x => x instanceof TokenDocument ? x.actor : undefined);
export function isItem(value) { return value instanceof Item; }
export const loadItem = typedLoad(isItem);
