import { FLAG_SCOPE } from "Util/Helper/FlagHelper";
export const MagickaFlagKey = "magicka";
export const DefaultMagickaFlag = {
    current: 0,
    max: 0
};
export function getMagicka(actor) {
    let base = actor.getFlag(FLAG_SCOPE, MagickaFlagKey) ?? DefaultMagickaFlag;
    return base;
}
export async function setMagicka(actor, flag) {
    await actor.setFlag(FLAG_SCOPE, MagickaFlagKey, flag);
}
