import LogFactory from "../Logging";
import { itemQty } from "../Extension/Items";
import { loadActor, loadItem } from "../Identifiers/UuidHelper";
const Log = LogFactory("ItemTransferHelper");
export function fixItemData(itemData, options) {
    itemData = deepClone(itemData);
    if (itemData.effects && !Array.isArray(itemData.effects)) {
        itemData.effects = Object.values(itemData.effects).map(x => x);
    }
    if (options?.qty) {
        itemData.system.quantity = options.qty;
    }
    return itemData;
}
function resolve(actorSource) {
    if (actorSource instanceof Actor)
        return actorSource;
    else
        return loadActor.sync(actorSource);
}
export async function addItem(actorSource, itemData, options = {}) {
    let actor = resolve(actorSource);
    let itemArr = (Array.isArray(itemData) ? itemData : [itemData]).map(i => fixItemData(i, options));
    let updating = [];
    let newArr = [];
    itemArr.forEach(i => {
        let existing = actor.items.find(x => x.name == i.name);
        if (existing) {
            updating.push({ _id: existing.id, "system.quantity": existing.qty() + (options.qty ?? i.data.quantity ?? 1) });
        }
        else {
            newArr.push(i);
        }
    });
    if (updating.length > 0) {
        await actor.updateEmbeddedDocuments("Item", updating);
    }
    if (newArr.length > 0) {
        await actor.createEmbeddedDocuments("Item", newArr);
    }
}
export async function removeItem(itemId, qty) {
    let item = await loadItem(itemId);
    if (!item)
        return null;
    let iQty = itemQty(item) || 1;
    if (qty >= iQty) {
        await item.delete();
    }
    else {
        await item.update({ "system.quantity": iQty - qty });
    }
    return item._source;
}
