import { miiQty } from "./MerchantInventoryItemData";
import { getBuyPrice } from "../../Flag/MerchantFlag";
import GoldDisplay from "../../../../Util/Components/GoldDisplay";
import ItemDescription from "../../../../Util/Components/ItemDescription";
export const MIIImageCol = { label: "", ColumnComponent: ({ item }) => (<img width="24px" height="24px" src={item.item.img}/>), cellProps: { width: 26 } };
export const MIINameCol = { label: "Name", ColumnComponent: ({ item }) => (<>{item.item.name}</>) };
export const MIIQtyCol = { label: "Qty", ColumnComponent: ({ item }) => (<>{miiQty(item)}</>) };
export const MIIPriceCol = { label: "Price", ColumnComponent: ({ item, merchantFlag, }) => (<GoldDisplay value={getBuyPrice(item, 1, merchantFlag)}/>) };
export function MIIExpander({ item }) {
    let description = item.item.system.description?.value ?? "No description found";
    return <ItemDescription description={description}/>;
}
export const DefaultMIICols = [
    MIIImageCol,
    MIINameCol,
    MIIQtyCol,
    MIIPriceCol
];
