import { useCallback, useMemo } from "react";
export function useStateUpdate(setState) {
    return useMemo(() => ({
        set: setState,
        update: setState
    }), [setState]);
}
function call(oldValue, x) {
    if (typeof x === "function") {
        let u = x;
        return u(oldValue);
    }
    else {
        return x;
    }
}
export function useMappedSetter(key, set) {
    return useCallback((x) => {
        set((oldT) => {
            let newT = { ...oldT };
            newT[key] = call(oldT[key], x);
            return newT;
        });
    }, [set, key]);
}
export function useSafeSetter(set, def) {
    return useCallback((x) => {
        set((oldT) => {
            let safeT = oldT ? oldT : def;
            return call(safeT, x);
        });
    }, [set, def]);
}
export function useSetter(value, set) {
    return useCallback((x) => {
        set(call(value, x));
    }, [value, set]);
}
