import { useIsGm } from "./GmContext";
import { useCallback } from "react";
import { IconButton } from "@material-ui/core";
const DefaultUser = { id: "default", name: "Default" };
var PermActive;
(function (PermActive) {
    PermActive[PermActive["NO"] = 0] = "NO";
    PermActive[PermActive["DEFAULT"] = 1] = "DEFAULT";
    PermActive[PermActive["YES"] = 2] = "YES";
})(PermActive || (PermActive = {}));
const PermissionStates = [
    { name: "None", icon: <i className="fas fa-times-circle"/> },
    { name: "Limited", icon: <i className="fas fa-question-circle"/> },
    { name: "Vision", icon: <i className="fas fa-eye"/> },
    { name: "Full", icon: <i className="fas fa-check-circle"/> }
];
function PermissionControlButton({ active, permissionKey, permissionData, setUserPermissionType }) {
    let setMe = useCallback(() => setUserPermissionType(permissionKey), [permissionKey, setUserPermissionType]);
    let color = undefined;
    if (active == PermActive.DEFAULT)
        color = "secondary";
    if (active == PermActive.YES)
        color = "primary";
    return <div><IconButton onClick={setMe} color={color}>
        {permissionData.icon}
    </IconButton></div>;
}
function PermissionControlRow({ user, state, setState }) {
    let defaultValue = state[DefaultUser.id];
    let myValue = state[user.id];
    let setMe = useCallback((value) => setState(user.id, value), [setState, user]);
    return <div style={{ display: "flex" }}>
        <div style={{ width: "100px" }}>{user.name}</div>
        {PermissionStates.map((value, index) => {
            let active = PermActive.NO;
            if (myValue === undefined && defaultValue === index)
                active = PermActive.DEFAULT;
            if (myValue === index) {
                active = PermActive.YES;
            }
            return <PermissionControlButton key={value.name} active={active} permissionData={value} permissionKey={index} setUserPermissionType={setMe}/>;
        })}
    </div>;
}
export function PermissionControls({ document }) {
    let isGm = useIsGm();
    if (!isGm)
        return null;
    let perms = document.ownership;
    let users = game.users?.contents?.filter(x => x !== game.user) ?? [];
    let updatePerms = useCallback(async (user, state) => {
        await document.update({ [`ownership.${user}`]: state });
    }, []);
    return <div>
        <PermissionControlRow user={DefaultUser} state={perms} setState={updatePerms}/>
        {users.map((user) => <PermissionControlRow key={user.uuid} user={user} state={perms} setState={updatePerms}/>)}
    </div>;
}
