export const FLAG_SCOPE = "MorrowinDnDReact";
export default function getFlag(object, flagId, defaultFlag) {
    let defFlag = defaultFlag || {};
    let objectFlag = object.getFlag(FLAG_SCOPE, flagId) ?? {};
    let flag = { ...defFlag, ...objectFlag };
    // @ts-ignore
    return [flag, (newFlag) => object.setFlag(FLAG_SCOPE, flagId, newFlag)];
}
