import { getterColumn } from "../Util/GetterColumn";
import { NumberFormat } from "../Util/Formats";
import { BasicImageColumn } from "../Util/BasicColumns";
import { mappedColumn } from "../Util/MapColumns";
export const ImageColumn = mappedColumn((item) => item.img, BasicImageColumn);
export const NameColumn = getterColumn("Name", (item) => item.name);
export const QtyColumn = getterColumn("Qty", (item) => item.qty());
export const WeightColumn = getterColumn("Weight", (item) => NumberFormat.format(item.weight()));
export const DefaultItemColumns = [
    ImageColumn,
    NameColumn,
    WeightColumn,
    QtyColumn
];
