import { migrateSettings } from "./SettingsMigrator";
import { FLAG_SCOPE } from "Util/Helper/FlagHelper";
export const SettingsModuleKey = FLAG_SCOPE;
let isInitialised = false;
const settings = [];
export function addSetting(x) {
    settings.push(x);
    if (isInitialised)
        x.register();
    return x;
}
export class BaseSetting {
    data;
    constructor(data) {
        this.data = data;
    }
    get value() {
        return isInitialised ? game.settings.get(SettingsModuleKey, this.data.key) : this.data.default;
    }
    set value(newValue) {
        game.settings.set(SettingsModuleKey, this.data.key, newValue);
    }
    register() { }
}
export class Setting extends BaseSetting {
    constructor(data) {
        super({ ...data, scope: data.scope ?? "world", config: data.config ?? true });
    }
    register() {
        game.settings.register(SettingsModuleKey, this.data.key, {
            ...this.data
        });
    }
}
export function setupSetting(data) {
    return addSetting(new Setting(data));
}
function init() {
    if (isInitialised)
        return;
    settings.forEach(x => x.register());
    isInitialised = true;
}
Hooks.on("init", init);
Hooks.on("ready", init);
Hooks.on("ready", migrateSettings);
