export function mappedColumn(mapper, column) {
    let Col = column.ColumnComponent;
    return {
        ...column,
        ColumnComponent: ({ item, index, ...extra }) => <Col index={index} item={mapper(item)} {...extra}/>
    };
}
export function mappedColumns(mapper, cols) {
    return cols.map((c) => mappedColumn(mapper, c));
}
