import { mappedColumn } from "../Util/MapColumns";
import { BasicImageColumn } from "../Util/BasicColumns";
import { getterColumn } from "../Util/GetterColumn";
export const ActorImageColumn = mappedColumn((item) => item.img, BasicImageColumn);
export const ActorNameColumn = getterColumn("Name", (item) => item.name);
export const ActorItemTableColumns = [
    ActorImageColumn,
    ActorNameColumn
];
