import { SimpleReactFormSheet } from "Util/React/ReactFormApplication";
import * as React from "react";
import { addSetting, BaseSetting, SettingsModuleKey, setupSetting } from "./Config";
export class SettingMenu extends BaseSetting {
    setting;
    constructor(setting, data) {
        super({ ...data, scope: data.scope ?? "world", config: true });
        this.setting = setting;
    }
    get value() {
        return this.setting.value;
    }
    set value(newValue) {
        this.setting.value = newValue;
    }
    register() {
        let Type = this.data.type;
        let self = this;
        class TempClass extends SimpleReactFormSheet {
            constructor() {
                super(<Type setting={self.setting}/>);
            }
            static get defaultOptions() {
                return mergeObject(super['defaultOptions'], {
                    ...self.data.sheetOptions || {}
                });
            }
        }
        game.settings.registerMenu(SettingsModuleKey, this.data.key, {
            ...this.data,
            type: TempClass
        });
    }
}
export function setupSettingMenu(data) {
    let settingData = {
        ...data,
        key: data.key + '.value',
        config: false,
        type: null
    };
    let setting = setupSetting(settingData);
    return addSetting(new SettingMenu(setting, data));
}
