import getFlag, { FLAG_SCOPE } from "../../../Util/Helper/FlagHelper";
const GroupPayFlagKey = "GroupPay";
export function hasGroupPayFlag(message) {
    return message.getFlag(FLAG_SCOPE, GroupPayFlagKey) !== undefined;
}
export function useGroupPayFlag(message) {
    return getFlag(message, GroupPayFlagKey);
}
export function getGroupPayFlagData(request) {
    let flag = {
        request,
        pledges: [],
        paid: false
    };
    return {
        flags: {
            [FLAG_SCOPE]: {
                [GroupPayFlagKey]: flag
            }
        }
    };
}
export function getTotalPledged(pledged) {
    if (!pledged)
        return 0;
    return pledged.reduce((p, c) => p + c.amount, 0);
}
