import { ReactObj } from "Util/React/ReactMixin";
export default class ReactFormApplication extends FormApplication {
    reactObj = new ReactObj(this);
    static get defaultOptions() {
        return mergeObject(super.defaultOptions, {
            template: "modules/MorrowinDnDReact/templates/ReactApplication.hbs"
        });
    }
    superRender = super._render;
    getComponent() {
        throw Error("No component defined!");
    }
    getData() {
        return { id: this.appId };
    }
    async _render(force, ...args) {
        await this.reactObj.render(force, ...args);
    }
}
export class SimpleReactFormSheet extends ReactFormApplication {
    component;
    constructor(component, a, b) {
        super(a, b);
        this.component = component;
    }
    getComponent() {
        return this.component;
    }
    async _updateObject(event, formData) {
        return null;
    }
}
