import { itemPrice } from "Util/Extension/Items";
const defaultMerchantFlag = {
    items: [],
    buyRate: 3,
    sellRate: 0.2
};
export function getMerchantFlag(actor) {
    let flag = { ...defaultMerchantFlag, ...(actor.getFlag("morrowindnd", "MerchantSheetData") || {}) };
    return [flag, (newFlag) => actor.setFlag("morrowindnd", "MerchantSheetData", newFlag)];
}
export function getBuyPrice(item, qty, merchantFlag) {
    let flag = merchantFlag || defaultMerchantFlag;
    return itemPrice(item) * qty * flag.buyRate;
}
export function getSellPrice(item, qty, merchantFlag) {
    let flag = merchantFlag || defaultMerchantFlag;
    return itemPrice(item) * qty * flag.sellRate;
}
