import { useCallback } from "react";
export function useArrayRemover(setter) {
    return useCallback((slot) => {
        setter(original => {
            let newArray = [...original];
            newArray.splice(slot, 1);
            return newArray;
        });
    }, [setter]);
}
export function useArrayAdder(setter) {
    return useCallback((newValue) => {
        let x = Array.isArray(newValue) ? newValue : [newValue];
        setter((original) => [...original, ...x]);
    }, [setter]);
}
export function useArrayUpdater(setter) {
    return useCallback((slot, newValue) => {
        setter((original) => {
            let newTables = [...original];
            newTables[slot] = newValue;
            return newTables;
        });
    }, [setter]);
}
export function useArrayReducers(setter) {
    let update = useArrayUpdater(setter);
    let adder = useArrayAdder(setter);
    let remover = useArrayRemover(setter);
    return [update, adder, remover];
}
export function useArrayEnable(setter) {
    return (newValue) => {
        setter((oldArray) => {
            if (oldArray.includes(newValue))
                return oldArray;
            return [...oldArray, newValue];
        });
    };
}
export function useArrayDisable(setter) {
    return (removedValue) => {
        setter((oldArray) => {
            if (!oldArray.includes(removedValue))
                return oldArray;
            return oldArray.filter(x => x !== removedValue);
        });
    };
}
export function useArrayEnableDisable(setter) {
    return [useArrayEnable(setter), useArrayDisable(setter)];
}
